body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace;
}

.search-location-container {
  border-radius: 10px;
  padding-left: 8px;
  padding-right: 8px;
  display: flex;
  position: relative;
  box-shadow: 0 8px 24px rgba(149, 157, 165, .2);
}

.search-input {
  height: 100%;
  width: 100%;
  border: none;
}

.search-input:focus {
  outline: none;
}

.estimate-button {
  color: #fff;
  text-transform: capitalize;
  width: 210px;
  cursor: pointer;
  background-color: #044fff;
  border: none;
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  margin-top: 8px;
  margin-bottom: 8px;
  padding: 10px;
  font-weight: 600;
  display: flex;
}

.arrow-container {
  color: #fff;
  background: #0040cc;
  border-radius: 10px;
  padding: 4px;
  display: flex;
}

.locations-container {
  width: 100%;
  background: #fff;
  border-radius: 12px;
  padding: 0;
  list-style-type: none;
  position: absolute;
  top: 60px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .15);
}

.locations-container > li {
  cursor: pointer;
  margin-top: 5px;
  padding: 6px;
}

.locations-container > li:hover {
  background: gray;
}

.details-text {
  color: gray;
  margin-left: 10px;
}

